import PropTypes from 'prop-types'
import * as types from './constants'
import * as Shape from './export'

export default function renderer({type, info}) {
  switch (type) {
    case types.LINE:
      return <Shape.LineVertex {...info} />
    case types.SHAPE:
      return <Shape.ShapeVertex {...info} />
    default:
      return null
  }
}

renderer.propTypes = {
  type: PropTypes.string.isRequired,
  info: PropTypes.object.isRequired,
}
