import PropTypes from 'prop-types'

export default function LineVertex({id1, x1, y1, id2, x2, y2, selected}) {
  const style = {
    fill: 'rgb(0, 150, 253)',
    stroke: 'rgb(255, 255, 255)',
    strokeWidth: 1,
  }

  return (
    <>
      <g
        transform={`translate(${x1}, ${y1})`}
        data-element-root="true"
        data-prototype="vertices"
        data-id={id1}
        data-selected={selected}
        data-layer="layer-1">
        <circle cx="0" cy="0" r={7} style={style}></circle>
      </g>
      <g
        transform={`translate(${x2}, ${y2})`}
        data-element-root="true"
        data-prototype="vertices"
        data-id={id2}
        data-selected={selected}
        data-layer="layer-1">
        <circle cx="0" cy="0" r={7} style={style}></circle>
      </g>
    </>
  )
}

LineVertex.defaultProps = {
  id1: PropTypes.string.isRequired,
  x1: PropTypes.number.isRequired,
  y1: PropTypes.number.isRequired,
  id2: PropTypes.string.isRequired,
  x2: PropTypes.number.isRequired,
  y2: PropTypes.number.isRequired,
  selected: PropTypes.bool,
}

LineVertex.defaultProps = {
  selected: true,
}
