import PropTypes from 'prop-types'
import {
  angleBetweenTwoPointsAndOrigin, pointsDistance, samePoints
} from  '../../utils/geometry'

export default function Line({id, x1, y1, x2, y2, selected}) {
  const height = 5
  const enableSnap = false

  if (samePoints({x: x1, y: y1}, {x: x2, y: y2})) return null

  if (enableSnap) {
    const distance = 10
    if (x2 < (x1 + distance) && x2 > (x1 - distance)) {
      x2 = x1
    }
    if (y2 < (y1 + distance) && y2 > (y1 - distance)) {
      y2 = y1
    }
  }

  let length = pointsDistance(x1, y1, x2, y2)
  let angle = angleBetweenTwoPointsAndOrigin(x1, y1, x2, y2);

  let pos = 'left'
  if (angle > -90 && angle < 90) {
    pos = 'right'
  }
  const transX = (val) => {
    if ('left' === pos) {
      return -val
    }
    return val
  }

  return (
    <g opacity="1">
      <g transform={`translate(${x1}, ${y1}) rotate(${angle}, 0, 0)`} data-element-root="true" data-prototype="lines" data-id={id} data-selected={selected} data-layer="layer-1">
        {/* the ruler */}
        {selected && <g transform={`translate(0,${'left' === pos ? (height / 2) + 10 : -((height / 2) + 10)})${'left' === pos ? 'rotate(180,0,0)' : ''}`}>
          <text x={transX(length / 2)} y="-3" transform="scale(1, 1)" style={{textAnchor: 'middle', fontSize: 12, pointerEvents: 'none', fontWeight: 'bold', userSelect: 'none'}}>{length.toFixed(2)} cm</text>
          <line x1="0" y1="-5" x2="0" y2="5" style={{stroke: 'rgb(0, 150, 253)', strokeWidth: 1}}></line>
          <line x1={transX(length)} y1="-5" x2={transX(length)} y2="5" style={{stroke: 'rgb(0, 150, 253)', strokeWidth: 1}}></line>
          <line x1="0" y1="0" x2={transX(length)} y2="0" style={{stroke: 'rgb(0, 150, 253)', strokeWidth: 1}}></line>
        </g>}

        {/* the line */}
        <g>
          <rect x="0" y={-(height / 2)} width={length} height={height} style={{strokeWidth: 1, stroke: selected ? 'rgb(153, 195, 251)' : 'none', fill: '#888'}}></rect>
        </g>
      </g>
    </g>
  )
}

Line.propTypes = {
  id: PropTypes.string.isRequired,
  x1: PropTypes.number.isRequired,
  y1: PropTypes.number.isRequired,
  x2: PropTypes.number.isRequired,
  y2: PropTypes.number.isRequired,
  selected: PropTypes.bool,
}

Line.defaultProps = {
  selected: true,
}
