import PropTypes from 'prop-types'
import { useRef } from 'react'
import {
  RESIZE_NORTHWEST, RESIZE_NORTHEAST, RESIZE_SOUTHWEST, RESIZE_SOUTHEAST,
  RESIZE_NORTH, RESIZE_SOUTH, RESIZE_WEST, RESIZE_EAST
} from '../../scene/constants'

export default function ShapeVertex({id, x, y, width, height, rotate}) {
  const ref = useRef()
  const radius = 7

  const dispatchEvent = (name) => {
    let evt = new Event('resize-shape-event')
    evt.direction = name
    document.dispatchEvent(evt)
  }

  const dispatchRotateEvent = () => {
    let evt = new Event('rotate-shape-event')
    if (ref.current) {
      const node = ref.current
      node.setAttribute('data-isrotate', true)
    }
    document.dispatchEvent(evt)
  }

  const onMouseUp = () => {
    if (ref.current) {
      const node = ref.current
      node.setAttribute('data-isrotate', false)
    }
  }

  const rotateX = x + (width / 2)
  const rotateY = y + (height / 2)

  return (
    <g ref={ref} data-element-root="true" data-prototype="shape-vertices" data-id={id} data-selected={true} id="diagram_diagramAdorner" style={{pointerEvents: 'all'}}>
      <g id="diagram_SelectorElement" transform={`rotate(${rotate},${rotateX},${rotateY})`}>
        <line stroke="black" strokeWidth={1} strokeDasharray="2,3" fill="None" id="pivotLine" x1={x + (width / 2)} y1={y - 25} x2={x + (width / 2)} y2={y} opacity={1} transform="rotate(0 246.5 255)" visibility="visible" className="e-diagram-pivot-line" />
        <circle onMouseDown={dispatchRotateEvent} onMouseUp={onMouseUp} cx={x + (width / 2)} cy={y - 25} r={radius} stroke="#0096fd" strokeWidth={1} strokeDasharray="none" fill="white" id="rotateThumb" visibility="visible" className="e-diagram-resize-handle e-north" aria-label="Thumb to resize the selected object on top side direction" style={{display: 'block'}} />
        <rect id="borderRect" x={x} y={y} width={width} height={height} visibility="visible" rx={0} ry={0} opacity={1} aria-label="" className="e-diagram-border" pointerEvents="none" stroke="black" strokeWidth="1.2" strokeDasharray="6,3" fill="transparent" />
        <circle onMouseDown={() => dispatchEvent(RESIZE_NORTHWEST)} cx={x} cy={y} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeNorthWest" visibility="visible" className="e-diagram-resize-handle e-northwest" aria-label="Thumb to resize the selected object on top left side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_NORTHEAST)} cx={x + width} cy={y} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeNorthEast" visibility="visible" className="e-diagram-resize-handle e-northeast" aria-label="Thumb to resize the selected object on top right side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_SOUTHWEST)} cx={x} cy={y + height} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeSouthWest" visibility="visible" className="e-diagram-resize-handle e-southwest" aria-label="Thumb to resize the selected object on bottom left side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_SOUTHEAST)} cx={x + width} cy={y+height} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeSouthEast" visibility="visible" className="e-diagram-resize-handle e-southeast" aria-label="Thumb to resize the selected object on bottom right side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_NORTH)} cx={x + (width / 2)} cy={y} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeNorth" visibility="visible" className="e-diagram-resize-handle e-north" aria-label="Thumb to resize the selected object on top side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_SOUTH)} cx={x + (width / 2)} cy={y + height} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeSouth" visibility="visible" className="e-diagram-resize-handle e-south" aria-label="Thumb to resize the selected object on bottom side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_WEST)} cx={x} cy={y + (height / 2)} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeWest" visibility="visible" className="e-diagram-resize-handle e-west" aria-label="Thumb to resize the selected object on left side direction" style={{display: 'block'}} />
        <circle onMouseDown={() => dispatchEvent(RESIZE_EAST)} cx={x + width} cy={y + (height / 2)} r={radius} stroke="white" strokeWidth={1} strokeDasharray="none" fill="#0096fd" id="resizeEast" visibility="visible" className="e-diagram-resize-handle e-east" aria-label="Thumb to resize the selected object on right side direction" style={{display: 'block'}} />
      </g>
    </g>
  )
}

ShapeVertex.defaultProps = {
  id: PropTypes.string.isRequired,
  x: PropTypes.number.isRequired,
  y: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  rotate: PropTypes.number.isRequired,
  selected: PropTypes.bool,
}

ShapeVertex.defaultProps = {
  width: 100,
  height: 100,
  rotate: 0,
}
