import { useState, forwardRef, useEffect } from 'react'
import { nanoid } from 'nanoid'
import PropTypes from 'prop-types'
import ReactSVGPanZoom from './react-svg-pan-zoom/viewer'

import { Grid as Diagram, RulerX, RulerY } from './diagram/export'
import { Line } from './connectors/export'
import BasicShape from './basic-shapes/renderer'
import Vertex from './vertices/renderer'
import * as vertexType from './vertices/constants'
import * as sceneMode from '../scene/constants'
import * as basicShapeType from './basic-shapes/constants'

const Viewer = forwardRef(({rWidth, rHeight}, ref) => {
  const [val, setVal] = useState({a: 1, b: 0, c: 0, d: 1, e: 0, f: 0})
  const [opacity, setOpacity] = useState(1)
  const [mouseCoord, setMouseCoord] = useState({x: 0, y:0 })
  const [sceneZoom, setSceneZoom] = useState(1)
  const [rulerMarkerShow, setRulerMarkerShow] = useState(false)
  const [mode, setMode] = useState(sceneMode.MODE_IDLE)
  const [drawingLine, setDrawingLine] = useState({x1: 0, y1: 0, x2: 0, y2: 0})
  const [lines, setLines] = useState([])
  const [drawingItem, setDrawingItem] = useState(null)
  const [items, setItems] = useState([])
  const [itemType, setItemType] = useState('')
  const [snap, setSnap] = useState({})
  const [selected, setSelected] = useState(null)
  const [tool, setTool] = useState('auto')
  const [vertices, setVertices] = useState({})
  const [draggingSupport, setDraggingSupport] = useState(null)
  const [resizeDirection, setResizeDirection] = useState('')
  const [rotateAngle, setRotateAngle] = useState(0)
  const [rotatingSupport, setRotatingSupport] = useState(null)
  const enableSnap = false
  const continuousDraw = false

  const width = 5000
  const height = 5000
  const rulerSize = 25
  const rulerUnitPixelSize = 100
  const sceneWidth = val.SVGWidth || width
  const sceneHeight = val.SVGHeight || height
  const rulerXElements = Math.ceil( sceneWidth / rulerUnitPixelSize )
  const rulerYElements = Math.ceil( sceneHeight / rulerUnitPixelSize ) + 1
  const deductWidth = 350
  const deductHeight = 0

  useEffect(() => {
    window.addEventListener('keydown', keyboardEvent)
    return () => window.removeEventListener('keydown', keyboardEvent)
  }, [selected])

  useEffect(() => {
    document.addEventListener('resize-shape-event', resizeEventListener)
  }, [resizeDirection])

  useEffect(() => {
    document.addEventListener('rotate-shape-event', rotateEventListener)
  }, [rotateAngle])

  const resizeEventListener = (event) => {
    setResizeDirection(event.direction)
  }

  const rotateEventListener = () => {
    setMode(sceneMode.MODE_ROTATING_SHAPE)
    setRotateAngle(180)
  }

  const keyboardEvent = (e) => {
    switch (e.keyCode) {
      case 27:
        setMode(sceneMode.MODE_IDLE)
        setDrawingLine({x1: 0, y1: 0, x2: 0, y2: 0})
        setDrawingItem(null)
        setSnap({})
        setVertices({})
        setSelected(null)
        break
      case 46:
        if (selected) {
          switch (selected.prototype) {
            case 'lines':
              const index = lines.findIndex(item => item.id === selected.id)
              lines.splice(index, 1)
              setVertices({})
              setSelected(null)
              break
            case 'items':
              const itemIndex = items.findIndex(item => item.id === selected.id)
              items.splice(itemIndex, 1)
              setVertices({})
              setSelected(null)
              break
            default: break
          }
        }
        break
      default:
        break
    }
  }

  const onMouseMove = (e) => {
    setMouseCoord({x: e.x, y: e.y})

    switch (mode) {
      case sceneMode.MODE_DRAWING_LINE:
        setDrawingLine({...drawingLine, x2: e.x, y2: e.y})
        const distance = 10
        let info = {}
        if (enableSnap) {
          if (e.x < (drawingLine.x1 + distance) && e.x > (drawingLine.x1 - distance)) {
            setSnap({dir: 'vertical', x: drawingLine.x1})
            info = {...vertices.info, ...drawingLine, x2: drawingLine.x1, y2: drawingLine.y1}
          } else if (e.y < (drawingLine.y1 + distance) && e.y > (drawingLine.y1 - distance)) {
            setSnap({dir: 'horizontal', y: drawingLine.y1})
            info = {...vertices.info, ...drawingLine, x2: mouseCoord.x, y2: mouseCoord.y}
          } else {
            setSnap({})
            info = {...vertices.info, ...drawingLine, x2: drawingLine.x2, y2: drawingLine.y2}
          }
        } else {
          setSnap({})
          info = {...vertices.info, ...drawingLine, x2: drawingLine.x2, y2: drawingLine.y2}
        }
        setVertices({type: vertexType.LINE, info})
        break
      case sceneMode.MODE_DRAWING_ITEM:
        setDrawingItem({id: nanoid(), x: mouseCoord.x, y: mouseCoord.y, type: itemType})
        break
      case sceneMode.MODE_DRAGGING_LINE:
        const diffX = mouseCoord.x - draggingSupport.startPointX
        const diffY = mouseCoord.y - draggingSupport.startPointY
        let newVertex0X = draggingSupport.startVertex0X + diffX
        let newVertex0Y = draggingSupport.startVertex0Y + diffY
        let newVertex1X = draggingSupport.startVertex1X + diffX
        let newVertex1Y = draggingSupport.startVertex1Y + diffY

        const index = lines.findIndex(item => item.id === draggingSupport.lineId)
        if (index !== -1) {
          lines[index] = {...lines[index], selected: true,
            x1: newVertex0X, y1: newVertex0Y, x2: newVertex1X, y2: newVertex1Y
          }
          setVertices({...vertices, info: {
            ...vertices.info,
            x1: newVertex0X, y1: newVertex0Y,
            x2: newVertex1X, y2: newVertex1Y
          }})
        }
        break
      case sceneMode.MODE_DRAGGING_VERTEX:
        let lineVertexPos
        if (draggingSupport.vertexId === vertices.info.id1) {
          setVertices({...vertices, info: {...vertices.info, x1: mouseCoord.x, y1: mouseCoord.y}})
          lineVertexPos = 1
        }
        if (draggingSupport.vertexId === vertices.info.id2) {
          setVertices({...vertices, info: {...vertices.info, x2: mouseCoord.x, y2: mouseCoord.y}})
          lineVertexPos = 2
        }
        switch (draggingSupport.prototype) {
          case 'lines':
            const lineIndex = lines.findIndex(line => line.id === vertices.target.id)
            let clonedLines = [...lines]
            if (lineVertexPos === 1) {
              clonedLines[lineIndex] = {...clonedLines[lineIndex], x1: mouseCoord.x, y1: mouseCoord.y}
            } else if (lineVertexPos === 2) {
              clonedLines[lineIndex] = {...clonedLines[lineIndex], x2: mouseCoord.x, y2: mouseCoord.y}
            }
            setLines(clonedLines)
            break
          default: break
        }
        break
      case sceneMode.MODE_DRAGGING_ITEM:
        const item = items.find(item => item.id === draggingSupport.id)
        const moveX = item.originalX - (draggingSupport.startPointX - e.x)
        const moveY = item.originalY - (draggingSupport.startPointY - e.y)
        item.x = moveX
        item.y = moveY
        setVertices({})
        break
      case sceneMode.MODE_DRAGGING_SHAPE_VERTEX:
        switch (resizeDirection) {
          case sceneMode.RESIZE_EAST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const size = item.originalWidth + (e.x - (item.x + item.originalWidth))
            if (size > 0) {
              item.width = size
              vertices.info.width = size
            }
            break
          }
          case sceneMode.RESIZE_WEST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const width = item.originalWidth + (item.originalX - e.x)
            if (width > 0) {
              item.x = e.x
              item.width = width
              vertices.info.x = e.x
              vertices.info.width = width
            }
            break
          }
          case sceneMode.RESIZE_SOUTH: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const size = item.originalHeight + (e.y - (item.y + item.originalHeight))
            if (size > 0) {
              item.height = size
              vertices.info.height = size
            }
            break
          }
          case sceneMode.RESIZE_NORTH: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const height = item.originalHeight + (item.originalY - e.y)
            if (height > 0) {
              item.y = e.y
              item.height = height
              vertices.info.y = e.y
              vertices.info.height = height
            }
            break
          }
          case sceneMode.RESIZE_NORTHEAST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const height = item.originalHeight + (item.originalY - e.y)
            const size = item.originalWidth + (e.x - (item.x + item.originalWidth))
            if (height > 0) {
              item.y = e.y
              item.height = height
              vertices.info.y = e.y
              vertices.info.height = height
            }
            if (size > 0) {
              item.width = size
              vertices.info.width = size
            }
            break
          }
          case sceneMode.RESIZE_NORTHWEST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const height = item.originalHeight + (item.originalY - e.y)
            const width = item.originalWidth + (item.originalX - e.x)
            if (height > 0) {
              item.y = e.y
              item.height = height
              vertices.info.y = e.y
              vertices.info.height = height
            }
            if (width > 0) {
              item.x = e.x
              item.width = width
              vertices.info.x = e.x
              vertices.info.width = width
            }
            break
          }
          case sceneMode.RESIZE_SOUTHEAST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const size = item.originalHeight + (e.y - (item.y + item.originalHeight))
            if (size > 0) {
              item.height = size
              vertices.info.height = size
            }
            const size2 = item.originalWidth + (e.x - (item.x + item.originalWidth))
            if (size2 > 0) {
              item.width = size2
              vertices.info.width = size2
            }
            break
          }
          case sceneMode.RESIZE_SOUTHWEST: {
            const item = items.find(item => item.id === draggingSupport.itemId)
            const size = item.originalHeight + (e.y - (item.y + item.originalHeight))
            if (size > 0) {
              item.height = size
              vertices.info.height = size
            }
            const width = item.originalWidth + (item.originalX - e.x)
            if (width > 0) {
              item.x = e.x
              item.width = width
              vertices.info.x = e.x
              vertices.info.width = width
            }
            break
          }
          default: break
        }
        break
      case sceneMode.MODE_ROTATING_SHAPE: {
        const item = items.find(item => item.id === rotatingSupport.itemId)
        let deltaX = e.x - item.x - (item.width / 2);
        let deltaY = e.y - item.y - (item.height / 2);
        let rotation = Math.atan2(deltaY, deltaX) * 180 / Math.PI;
        rotation += 90
        if (-5 < rotation && rotation < 5) rotation = 0;
        if (85 < rotation && rotation < 90) rotation = 90;
        if (175 < rotation && rotation < 185) rotation = 180;
        if (265 < rotation && rotation < 270) rotation = 270;
        item.rotate = rotation
        vertices.info.rotate = rotation
        break
      }
      default:
        break
    }
  }

  const onChangeValue = (value) => {
    if ((value.a * 100) <= 1000 && (value.a > 0.25)) {
      setVal(formatValue(value))
      setSceneZoom(value.a)
    }
  }

  const formatValue = (value) => {
    const zeroLeftPosition = width * sceneZoom || 0
    const zeroTopPosition = height * sceneZoom || 0
    const limitWidth = -Math.abs(zeroLeftPosition - value.viewerWidth)
    const limitHeight = -Math.abs(zeroTopPosition - value.viewerHeight)
    let newE = value.e
    let newF = value.f
    if (value.e > 0) {
      newE = 0
    } else if (limitWidth > value.e) {
      newE = limitWidth
    }

    if (value.f > 0) {
      newF = 0
    } else if (limitHeight > value.f) {
      newF = limitHeight
    }
    return {...value, e: newE, f: newF}
  }

  const onMouseDown = (e) => {
    setRulerMarkerShow(false)
    switch (mode) {
      case sceneMode.MODE_WAITING_DRAWING_LINE:
        const {x, y} = mouseCoord
        setDrawingLine({x1: x, y1: y, x2: x, y2: y})
        setVertices({type: vertexType.LINE, info: {
          id1: nanoid(), x1: x, y1: y,
          id2: nanoid(), x2: x, y2: y,
        }})
        setMode(sceneMode.MODE_DRAWING_LINE)
        break
      case sceneMode.MODE_DRAWING_LINE:
        if (!continuousDraw) {
          const lineId = nanoid()
          setLines([...lines, { id: lineId, selected: false,
            x1: drawingLine.x1, y1: drawingLine.y1,
            x2: mouseCoord.x, y2: mouseCoord.y,
          }])
          setVertices({})
          setMode(sceneMode.MODE_WAITING_DRAWING_LINE)
        }
        break
      case sceneMode.MODE_DRAWING_ITEM:
        const itemId = nanoid()
        setItems([...items, {id: itemId,
          x: mouseCoord.x, y: mouseCoord.y,
          width: 100, height: 100,
          type: itemType, selected: false,
          originalWidth: 100, originalHeight: 100,
          originalX: e.x, originalY: e.y,
        }])
        break
      case sceneMode.MODE_IDLE:
        const event = e.originalEvent
        const elementData = extractElementData(event.target)
        if (!elementData || !elementData.selected) return
        setTool('none')
        switch (elementData.prototype) {
          case 'lines':
            const line = lines.find(item => item.id === elementData.id)
            if (line) {
              setDraggingSupport({lineId: line.id,
                startPointX: mouseCoord.x, startPointY: mouseCoord.y,
                startVertex0X: line.x1, startVertex0Y: line.y1,
                startVertex1X: line.x2, startVertex1Y: line.y2,
              })
              setMode(sceneMode.MODE_DRAGGING_LINE)
            }
            break
          case 'vertices':
            setDraggingSupport({
              vertexId: elementData.id,
              prototype: 'lines',
            })
            setMode(sceneMode.MODE_DRAGGING_VERTEX)
            break
          case 'items':
            setDraggingSupport({
              id: elementData.id,
              prototype: 'items',
              startPointX: e.x,
              startPointY: e.y,
            })
            setMode(sceneMode.MODE_DRAGGING_ITEM)
            break
          case 'shape-vertices':
            if (elementData.is_rotate) {
              setRotatingSupport({
                id: elementData.id,
                prototype: 'shape-rotate-thumb',
                itemId: selected.id,
              })
              setMode(sceneMode.MODE_ROTATING_SHAPE)
            } else {
              setDraggingSupport({
                id: elementData.id,
                prototype: 'shape-vertex',
                itemId: selected.id,
              })
              setMode(sceneMode.MODE_DRAGGING_SHAPE_VERTEX)
            }
            break
          default: break
        }
        break
      default:
        break
    }
  }

  const onMouseUp = (e) => {
    setRulerMarkerShow(true)
    switch (mode) {
      case sceneMode.MODE_DRAWING_LINE:
        const lineId = nanoid()
        setLines([...lines, {
          id: lineId, selected: false,
          x1: drawingLine.x1, y1: drawingLine.y1,
          x2: mouseCoord.x, y2: mouseCoord.y,
        }])
        setVertices({
          type: vertexType.LINE, info: {
            id1: vertices.info.id1, x1: mouseCoord.x, y1: mouseCoord.y,
            id2: vertices.info.id2, x2: drawingLine.x1, y2: drawingLine.y1,
          },
          target: { id: lineId, type: 'lines' },
        })

        let x2 = mouseCoord.x
        let y2 = mouseCoord.y

        if (enableSnap) {
          const distance = 10
          if (x2 < (drawingLine.x1 + distance) && x2 > (drawingLine.x1 - distance)) {
            x2 = drawingLine.x1
          }
          if (y2 < (drawingLine.y1 + distance) && y2 > (drawingLine.y1 - distance)) {
            y2 = drawingLine.y1
          }
        }
        setDrawingLine({x1: x2, y1: y2, x2: x2, y2: y2})
        break
      case sceneMode.MODE_IDLE:
        const event = e.originalEvent
        const elementData = extractElementData(event.target)
        switch (elementData ? elementData.prototype : 'none') {
          case 'lines':
            const line = lines.find(item => item.id === elementData.id)
            if (line) {
              if (selected && selected.id === line.id) {
                setSelected(null)
                setVertices({})
              } else {
                setSelected({
                  id: line.id,
                  prototype: 'lines',
                })
                setVertices({type: vertexType.LINE, info: {
                  id1: nanoid(), x1: line.x1, y1: line.y1,
                  id2: nanoid(), x2: line.x2, y2: line.y2},
                  target: {id: line.id, type: 'lines'}
                })
              }
            }
            break
          case 'items':
            const item = items.find(item => item.id === elementData.id)
            if (item) {
              const {x, y, width, height, rotate} = item
              setSelected({
                id: item.id,
                prototype: 'items',
              })
              setVertices({type: vertexType.SHAPE, info: {
                id: nanoid(), x, y, width, height, rotate
              }})
            }
            break
          case 'none':
            setSelected(null)
            setVertices({})
            setTool('auto')
            setDraggingSupport(null)
            break
          default: break
        }
        break
      case sceneMode.MODE_DRAGGING_LINE:
        setMode(sceneMode.MODE_IDLE)
        setSelected(null)
        setVertices({})
        setTool('auto')
        break
      case sceneMode.MODE_DRAGGING_VERTEX: {
        setMode(sceneMode.MODE_IDLE)
        setSelected(null)
        setVertices({})
        setDraggingSupport(null)
        setTool('auto')
        break
      }
      case sceneMode.MODE_DRAGGING_ITEM: {
        const item = items.find(item => item.id === selected.id)
        item.originalX = item.x
        item.originalY = item.y
        item.originalWidth = item.width
        item.originalHeight = item.height
        setMode(sceneMode.MODE_IDLE)
        setSelected(null)
        setVertices({})
        setTool('auto')
        break
      }
      case sceneMode.MODE_DRAGGING_SHAPE_VERTEX: {
        const item = items.find(item => item.id === selected.id)
        item.originalX = item.x
        item.originalY = item.y
        item.originalWidth = item.width
        item.originalHeight = item.height
        setMode(sceneMode.MODE_IDLE)
        setSelected(null)
        setVertices({})
        setTool('auto')
        break
      }
      case sceneMode.MODE_ROTATING_SHAPE: {
        const item = items.find(item => item.id === selected.id)
        item.originalX = item.x
        item.originalY = item.y
        item.originalWidth = item.width
        item.originalHeight = item.height
        setMode(sceneMode.MODE_IDLE)
        setSelected(null)
        setVertices({})
        setTool('auto')
        break
      }
      default: break
    }
  }

  //console.log(mode)

  const computedWidth = ((rWidth || 1) - deductWidth) - rulerSize
  const computedHeight = ((rHeight || 1) - deductHeight) - rulerSize

  return (
    <>
      <div ref={ref} style={{display: 'flex', flexFlow: 'row nowrap', height: '100vh'}}>
        <div style={{
          margin: 0,
          padding: 0,
          display: 'grid',
          gridRowGap: '0',
          gridColumnGap: '0',
          gridTemplateColumns: `${rulerSize}px ${((rWidth || 1) - deductWidth) - rulerSize}px`,
          gridTemplateRows: `${rulerSize}px ${((rHeight || 1) - deductHeight) - rulerSize}px`,
          position: 'relative'
        }}>
          <div style={{ gridColumn: 1, gridRow: 1, backgroundColor: '#f5f5f5' }}></div>
          <div style={{ gridRow: 1, gridColumn: 2, position: 'relative', overflow: 'hidden' }} id="rulerX">
            {sceneWidth && <RulerX
              unitPixelSize={rulerUnitPixelSize}
              positiveUnitsNumber={rulerXElements}
              zoom={sceneZoom}
              mouseX={mouseCoord.x}
              width={computedWidth}
              valueE={val.e}
              marker={rulerMarkerShow}/>}
          </div>
          <div style={{ gridColumn: 1, gridRow: 2, position: 'relative', overflow: 'hidden' }} id="rulerY">
            {sceneHeight && <RulerY
              unitPixelSize={rulerUnitPixelSize}
              positiveUnitsNumber={rulerYElements}
              zoom={sceneZoom}
              mouseY={mouseCoord.y}
              height={computedHeight}
              zeroTopPosition={height * sceneZoom}
              valueF={val.f}
              marker={rulerMarkerShow}/>}
          </div>
          <div style={{position: 'relative', width: computedWidth, height: computedHeight, gridColumn: 2, gridRow: 2}}>
            <ReactSVGPanZoom
              style={{ gridColumn: 2, gridRow: 2 }}
              width={((rWidth || 1) - deductWidth) - rulerSize}
              background="#fff"
              height={((rHeight || 1) - deductHeight) - rulerSize}
              value={val}
              onChangeValue={onChangeValue}
              tool={tool}
              detectAutoPan={false}
              disableDoubleClickZoomWithToolAuto={true}
              onChangeTool={tool => {}}
              onMouseMove={onMouseMove}
              onMouseDown={onMouseDown}
              onMouseUp={onMouseUp}
              onMouseEnter={() => setRulerMarkerShow(true)}
              onMouseLeave={() => setRulerMarkerShow(false)}
              miniatureProps={{position: 'none'}}
              toolbarProps={{position: 'none'}}
              grid={<Diagram width={width} height={height} opacity={opacity} zoom={sceneZoom} unitPixelSize={rulerUnitPixelSize} />}>
              <svg width={width} height={height}>
                {/* objects here */}
                {lines.map((item, index) => <Line key={index} {...item} selected={selected && 'lines' === selected.prototype && item.id === selected.id} />)}
                {drawingLine && <Line {...drawingLine} id={nanoid()} selected={sceneMode.MODE_DRAWING_LINE === mode} />}

                {items.map((item, index) => <BasicShape key={index} {...item} selected={selected && 'items' === selected.prototype && item.id === selected.id} />)}
                {false && drawingItem && <BasicShape {...drawingItem} />}
                {vertices && <Vertex {...vertices} />}
                {(!!snap && 'horizontal' === snap.dir) && <line data-snap="true" x1="0" y1={snap.y} x2={width} y2={snap.y} style={{stroke: 'rgb(211, 47, 47)', strokeWidth: 1}}></line>}
                {(!!snap && 'vertical' === snap.dir) && <line data-snap="true" x1={snap.x} y1="0" x2={snap.x} y2={height} style={{stroke: 'rgb(211, 47, 47)', strokeWidth: 1}}></line>}
              </svg>
            </ReactSVGPanZoom>
          </div>
        </div>

        <div style={{padding:10}}>
          <button style={{margin:5}} onClick={() => setOpacity(opacity ? 0 : 1)}>hide/show grid</button>
          <button style={{margin:5}} onClick={() => setMode(sceneMode.MODE_IDLE)}>select tool</button>
          <button style={{margin:5}} onClick={() => setMode(sceneMode.MODE_WAITING_DRAWING_LINE)}>draw line</button>

          {/* shape selection */}
          <select id="select-basic_shape" name="select-basic_shape" onChange={e => {
            setMode(sceneMode.MODE_DRAWING_ITEM)
            setItemType(e.target.value)
          }}>
            <option value="">--Select--</option>
            <option value={basicShapeType.RECTANGLE}>{basicShapeType.RECTANGLE}</option>
            <option value={basicShapeType.ELLIPSE}>{basicShapeType.ELLIPSE}</option>
            <option value={basicShapeType.HEXAGON}>{basicShapeType.HEXAGON}</option>
            <option value={basicShapeType.PARALLELOGRAM}>{basicShapeType.PARALLELOGRAM}</option>
            <option value={basicShapeType.TRIANGLE}>{basicShapeType.TRIANGLE}</option>
            <option value={basicShapeType.PLUS}>{basicShapeType.PLUS}</option>
            <option value={basicShapeType.STAR}>{basicShapeType.STAR}</option>
            <option value={basicShapeType.PENTAGON}>{basicShapeType.PENTAGON}</option>
            <option value={basicShapeType.HEPTAGON}>{basicShapeType.HEPTAGON}</option>
            <option value={basicShapeType.OCTAGON}>{basicShapeType.OCTAGON}</option>
            <option value={basicShapeType.TRAPEZOID}>{basicShapeType.TRAPEZOID}</option>
            <option value={basicShapeType.DECAGON}>{basicShapeType.DECAGON}</option>
            <option value={basicShapeType.RIGHT_TRIANGLE}>{basicShapeType.RIGHT_TRIANGLE}</option>
            <option value={basicShapeType.CYLINDER}>{basicShapeType.CYLINDER}</option>
            <option value={basicShapeType.DIAMOND}>{basicShapeType.DIAMOND}</option>
          </select>

          <div style={{marginTop:5}}>
            <span>x: </span><input type="text" value={mouseCoord.x} />
            <span>y: </span><input type="text" value={mouseCoord.y} />
            <small>{mode}</small>
            <br />

            <small>id1: {vertices?.info?.id1}</small>
            <br />
            <small>id2: {vertices?.info?.id2}</small>
          </div>
        </div>
      </div>
    </>
  )
})

function extractElementData(node) {
  while (!node.attributes.getNamedItem('data-element-root') && node.tagName !== 'svg') {
    node = node.parentNode
  }
  if (node.tagName === 'svg') return null

  const prototype = node.attributes.getNamedItem('data-prototype')?.value

  if ('shape-vertices' === prototype) {
    return {
      id: node.attributes.getNamedItem('data-id')?.value,
      prototype: node.attributes.getNamedItem('data-prototype')?.value,
      selected: node.attributes.getNamedItem('data-selected')?.value === 'true',
      is_rotate: node.attributes.getNamedItem('data-isrotate')?.value === 'true',
      direction: '',
    }
  } else {
    return {
      id: node.attributes.getNamedItem('data-id')?.value,
      prototype: node.attributes.getNamedItem('data-prototype')?.value,
      selected: node.attributes.getNamedItem('data-selected')?.value === 'true',
    }
  }
}

Viewer.propTypes = {
  rWidth: PropTypes.number.isRequired,
  rHeight: PropTypes.number.isRequired,
}

export default Viewer
