import PropTypes from 'prop-types'

export default function Grid({width, height, opacity, zoom, scales, unitPixelSize}) {
  let elementSize = unitPixelSize * zoom
  let scale = scales.filter(item => {
    if (zoom >= 1) {
      return 'zoom-in' === item.dir && elementSize > item.scale
    } else {
      return 'zoom-out' === item.dir && elementSize < item.scale
    }
  })
  scale = scale.length ? scale[scale.length - 1] : null

  return (
    <svg id="diagram_gridline_svg" width={width * zoom} height={height * zoom} opacity={opacity} className="e-grid-layer" style={{position: 'absolute', top: 0, left: 0, overflow: 'hidden', pointerEvents: 'none'}}>
      <g id="diagram_gridline" transform="translate(0,0)">
        <rect id="diagram_grid_rect" x="0" y="0" width="100%" height="100%" fill="url(#diagram_pattern)"></rect>
      </g>
      <defs id="diagram_grid_pattern_defn">
        <pattern id="diagram_pattern" x="0" y="0" width={scale.display * zoom} height={scale.display * zoom} patternUnits="userSpaceOnUse">
          <path strokeWidth="1.25" d={`M0,${scale.thickAdd * zoom} L${scale.display * zoom},${scale.thickAdd * zoom} Z`} className="e-diagram-thick-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M0,${(1 * (scale.step + scale.thinAdd)) * zoom} L${scale.display * zoom},${(1 * (scale.step + scale.thinAdd)) * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M0,${(2 * (scale.step + scale.thinAdd)) * zoom} L${scale.display * zoom},${(2 * (scale.step + scale.thinAdd)) * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M0,${(3 * (scale.step + scale.thinAdd)) * zoom} L${scale.display * zoom},${(3 * (scale.step + scale.thinAdd)) * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M0,${(4 * (scale.step + scale.thinAdd)) * zoom} L${scale.display * zoom},${(4 * (scale.step + scale.thinAdd)) * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="1.25" d={`M${scale.thickAdd * zoom},0 L${scale.thickAdd * zoom},${scale.display * zoom} Z`} className="e-diagram-thick-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M${(1 * (scale.step + scale.thinAdd)) * zoom},0 L${(1 * (scale.step + scale.thinAdd)) * zoom},${scale.display * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M${(2 * (scale.step + scale.thinAdd)) * zoom},0 L${(2 * (scale.step + scale.thinAdd)) * zoom},${scale.display * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M${(3 * (scale.step + scale.thinAdd)) * zoom},0 L${(3 * (scale.step + scale.thinAdd)) * zoom},${scale.display * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
          <path strokeWidth="0.25" d={`M${(4 * (scale.step + scale.thinAdd)) * zoom},0 L${(4 * (scale.step + scale.thinAdd)) * zoom},${scale.display * zoom} Z`} className="e-diagram-thin-grid" stroke="lightgray"></path>
        </pattern>
      </defs>
    </svg>
  )
}

Grid.propTypes = {
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  opacity: PropTypes.number.isRequired,
  zoom: PropTypes.number.isRequired,
  scales: PropTypes.array,
  unitPixelSize: PropTypes.number.isRequired,
}

Grid.defaultProps = {
  scales: [
    {
      scale: 0,
      display: 100,
      step: 20,
      thinAdd: 0.125,
      thickAdd: 1.625,
      dir: 'zoom-in',
    },
    {
      scale: 150,
      display: 50,
      step: 10,
      thinAdd: 0.125,
      thickAdd: 0.625,
      dir: 'zoom-in',
    },
    {
      scale: 250,
      display: 25,
      step: 5,
      thinAdd: 0.100,
      thickAdd: 0.225,
      dir: 'zoom-in',
    },
    {
      scale: 450,
      display: 12.5,
      step: 2.5,
      thinAdd: 0,
      thickAdd: 0.100,
      dir: 'zoom-in',
    },
    {
      scale: 100,
      display: 100,
      step: 20,
      thinAdd: 0.125,
      thickAdd: 0.625,
      dir: 'zoom-out',
    },
    {
      scale: 70,
      display: 200,
      step: 40,
      thinAdd: 0.125,
      thickAdd: 0.625,
      dir: 'zoom-out',
    },
    {
      scale: 40,
      display: 500,
      step: 100,
      thinAdd: 0.125,
      thickAdd: 1,
      dir: 'zoom-out',
    },
  ]
}
