import PropTypes from 'prop-types'
import { useRef } from 'react'
import { processPathData, splitArrayCollection, transformPath, getPathString, pathSegmentCollection } from '../../utils/path'
import { renderPath } from '../../utils/svg-renderer'

export default function Path({id, x, y, width, height, selected, draw, rotate}) {
  const ref = useRef()
  if (ref.current) {
    const node = ref.current
    const d = node.attributes.getNamedItem('d').value
    const bounds = node.getBBox()
    const newPath = updatePath(d, bounds, {width, height})
    let collection
    collection = processPathData(newPath)
    collection = pathSegmentCollection(collection)
    renderPath(node, null, collection)
  }

  const rotateX = x + (width / 2)
  const rotateY = y + (height / 2)

  return (
    <g opacity="1">
      <g id="diagram_diagramLayer" data-element-root="true" data-prototype="items" data-id={id} data-selected={selected} data-layer="layer-1">
        <g id="EllipseGtQ4N_groupElement" transform="translate(0,0)">
          <rect id="EllipseGtQ4N" x={x} y={y} width={width} height={height} visibility="visible" transform={`rotate(${rotate},${rotateX},${rotateY})`} rx={0} ry={0} opacity={1} aria-label="EllipseGtQ4N" stroke="transparent" strokeWidth={0} strokeDasharray="none" fill="transparent" />
          <g id="EllipseGtQ4N_content_groupElement">
            <path ref={ref} transform={`rotate(${rotate},${rotateX},${rotateY})translate(${x},${y})`}
              d={draw} id="EllipseGtQ4N_content" visibility="visible" opacity={1} aria-label="EllipseGtQ4N" stroke="#3A3A3A" strokeWidth={2} strokeDasharray="none" fill="white" />
          </g>
        </g>
      </g>
    </g>
  )
}

function updatePath(pathData, bounds, actualSize) {
  var _transformPath = true
  var _isDirt = true
  var isScale = false;
  var newPathString = '';
  var scaleX = -bounds.x;
  var scaleY = -bounds.y;
  var arrayCollection = [];
  if (actualSize.width !== bounds.width || actualSize.height !== bounds.height) {
    scaleX = actualSize.width / Number(bounds.width ? bounds.width : 1);
    scaleY = actualSize.height / Number(bounds.height ? bounds.height : 1);
    isScale = true;
  }
  arrayCollection = processPathData(pathData);
  arrayCollection = splitArrayCollection(arrayCollection);
  if ((isScale || _isDirt) && _transformPath) {
    newPathString = transformPath(arrayCollection, scaleX, scaleY, isScale, bounds.x, bounds.y, 0, 0);
  }
  else {
    newPathString = getPathString(arrayCollection);
  }
  isScale = false;
  return newPathString;
}

Path.propTypes = {
  id: PropTypes.string.isRequired,
  x: PropTypes.number.isRequired,
  y: PropTypes.number.isRequired,
  draw: PropTypes.string.isRequired,
  rotate: PropTypes.number.isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  selected: PropTypes.bool,
}

Path.defaultProps = {
  width: 100,
  height: 100,
  rotate: 0,
  selected: false,
}
