import PropTypes from 'prop-types'

export default function RulerY(props) {
  let elementH = props.unitPixelSize * props.zoom
  let scale = props.scales.filter(item => {
    if (props.zoom >= 1) {
      return 'zoom-in' === item.dir && elementH > item.scale
    } else {
      return 'zoom-out' === item.dir && elementH < item.scale
    }
  })
  scale = scale.length ? scale[scale.length - 1] : null
  let iterator = (props.unitPixelSize / (scale.display || 1)) * props.positiveUnitsNumber
      iterator = Math.ceil(iterator)

  const rulerStyle = {
    position: 'inherit',
    fontFamily: 'sans-serif',
    backgroundColor: props.backgroundColor,
  }

  const translateX = (val) => {
    const digits = val.toString().length
    return digits === 2 ? -17.5 :
      digits === 4 ? -34.859375 :
      digits === 5 ? -42.859375 :
      digits === 6 ? -49.859375 :
      digits === 7 ? -59.859375 : -25.859375
  }

  /* optimize ruler */
  const renderSegment = (val) => {
    const firstSegmentChecker = val > Math.abs(props.valueF) - (scale.display * props.zoom)
    const lastSegmentChecker = val < Math.abs(props.valueF) + props.height
    if (firstSegmentChecker && lastSegmentChecker) {
      return true
    }
    return false
  }

  return (
    <div>
      <svg id="diagram_vRuler_Ruler_svg" width="25px" height={props.height} style={rulerStyle}>
        <g transform={`matrix(1,0,0,1,0,${props.valueF})`}>
          {Array.from(Array(iterator), (e, i) => renderSegment(((i * scale.display) + scale.add) * props.zoom) && (
            <g className="e-ruler-segment" transform={`translate(0.5, ${((i * scale.display) + scale.add) * props.zoom})`} key={i}>
              <line x1="25" y1="0" x2="0" y2="0" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1="25" y1={(1 * scale.step) * props.zoom} x2="17.5" y2={(1 * scale.step) * props.zoom} strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1="25" y1={(2 * scale.step) * props.zoom} x2="17.5" y2={(2 * scale.step) * props.zoom} strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1="25" y1={(3 * scale.step) * props.zoom} x2="17.5" y2={(3 * scale.step) * props.zoom} strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1="25" y1={(4 * scale.step) * props.zoom} x2="17.5" y2={(4 * scale.step) * props.zoom} strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              {i === 0 ?
                <text style={{fontSize:'10pt',fill:props.fontColor}} className="e-ruler-tick-label" x="0" y="16.671875" transform="rotate(270)translate(-10.625,-4.171875)">{i * scale.display}</text> :
                <text style={{fontSize:'10pt',fill:props.fontColor}} className="e-ruler-tick-label" x="0" y="16.671875" transform={`rotate(270)translate(${translateX(i * scale.display)},-4.171875)`}>{i * scale.display}</text>}
            </g>
          ))}

          {props.marker && <line transform={`translate(0.5,${props.mouseY * props.zoom})`} id="diagram_wRuler_marker" x1="0" y1="0" x2="25" y2="0" stroke={props.markerColor} strokeWidth={1.5} className="e-d-ruler-marker"></line>}
        </g>
      </svg>
    </div>
  )
}

RulerY.propTypes = {
  scales: PropTypes.array,
  unitPixelSize: PropTypes.number.isRequired,
  positiveUnitsNumber: PropTypes.number,
  zoom: PropTypes.number.isRequired,
  mouseY: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  valueF: PropTypes.number.isRequired,
  backgroundColor: PropTypes.string,
  fontColor: PropTypes.string,
  markerColor: PropTypes.string,
  marker: PropTypes.bool,
}

RulerY.defaultProps = {
  positiveUnitsNumber: 50,
  backgroundColor: '#f5f5f5',
  fontColor: '#616161',
  markerColor: 'red',
  marker: false,
  scales: [
    {
      scale: 0,
      display: 100,
      step: 20,
      add: 0.5,
      dir: 'zoom-in',
    },
    {
      scale: 150,
      display: 50,
      step: 10,
      add: 0.5,
      dir: 'zoom-in',
    },
    {
      scale: 250,
      display: 25,
      step: 5,
      add: 0,
      dir: 'zoom-in',
    },
    {
      scale: 450,
      display: 12.5,
      step: 2.5,
      add: 0,
      dir: 'zoom-in',
    },
    {
      scale: 100,
      display: 100,
      step: 20,
      add: 0.5,
      dir: 'zoom-out',
    },
    {
      scale: 70,
      display: 200,
      step: 40,
      add: 0,
      dir: 'zoom-out',
    },
    {
      scale: 40,
      display: 500,
      step: 100,
      add: 0,
      dir: 'zoom-out',
    },
  ],
}
