import PropTypes from 'prop-types'

export default function RulerX(props) {
  let elementW = props.unitPixelSize * props.zoom
  let scale = props.scales.filter(item => {
    if (props.zoom >= 1) {
      return 'zoom-in' === item.dir && elementW > item.scale
    } else {
      return 'zoom-out' === item.dir && elementW < item.scale
    }
  })
  scale = scale.length ? scale[scale.length - 1] : null
  const iterator = (props.unitPixelSize / (scale.display || 1)) * props.positiveUnitsNumber

  const rulerStyle = {
    position: 'inherit',
    fontFamily: 'sans-serif',
    backgroundColor: props.backgroundColor,
  }

  /* optimize ruler */
  const renderSegment = (val) => {
    const firstSegmentChecker = val > Math.abs(props.valueE) - (scale.display * props.zoom)
    const lastSegmentChecker = val < Math.abs(props.valueE) + props.width
    if (firstSegmentChecker && lastSegmentChecker) {
      return true
    }
    return false
  }

  return (
    <div>
      <svg id="diagram_hRuler_Ruler_svg" width={props.width} height="25" style={rulerStyle}>
        <g transform={`matrix(1,0,0,1,${props.valueE},0)`}>
          {scale && Array.from(Array(iterator), (e, i) => renderSegment(((i * scale.display) + scale.add) * props.zoom) && (
            <g className="e-ruler-segment" transform={`translate(${((i * scale.display) + scale.add) * props.zoom}, 0.5)`} key={i}>
              <line x1="0" y1="25" x2="0" y2="0" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1={(1 * scale.step) * props.zoom} y1="25" x2={(1 * scale.step) * props.zoom} y2="17.5" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1={(2 * scale.step) * props.zoom} y1="25" x2={(2 * scale.step) * props.zoom} y2="17.5" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1={(3 * scale.step) * props.zoom} y1="25" x2={(3 * scale.step) * props.zoom} y2="17.5" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <line x1={(4 * scale.step) * props.zoom} y1="25" x2={(4 * scale.step) * props.zoom} y2="17.5" strokeWidth="1" stroke="black" className="e-ruler-tick"></line>
              <text style={{fontSize:'10pt',fill:props.fontColor}} className="e-ruler-tick-label" x="2" y="12.5">{i * scale.display}</text>
            </g>
          ))}

          {props.marker && <line transform={`translate(${(props.mouseX * props.zoom)},0.5)`} id="diagram_hRuler_marker" x1="0" y1="0" x2="0" y2="25" stroke={props.markerColor} strokeWidth={1.5} className="e-d-ruler-marker"></line>}
        </g>
      </svg>
    </div>
  )
}

RulerX.propTypes = {
  scales: PropTypes.array,
  unitPixelSize: PropTypes.number.isRequired,
  positiveUnitsNumber: PropTypes.number,
  zoom: PropTypes.number.isRequired,
  mouseX: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired,
  valueE: PropTypes.number.isRequired,
  backgroundColor: PropTypes.string,
  fontColor: PropTypes.string,
  markerColor: PropTypes.string,
  marker: PropTypes.bool,
}

RulerX.defaultProps = {
  positiveUnitsNumber: 50,
  backgroundColor: '#f5f5f5',
  fontColor: '#616161',
  markerColor: 'red',
  marker: false,
  scales: [
    {
      scale: 0,
      display: 100,
      step: 20,
      add: 0.5,
      dir: 'zoom-in',
    },
    {
      scale: 150,
      display: 50,
      step: 10,
      add: 0.5,
      dir: 'zoom-in',
    },
    {
      scale: 250,
      display: 25,
      step: 5,
      add: 0,
      dir: 'zoom-in',
    },
    {
      scale: 450,
      display: 12.5,
      step: 2.5,
      add: 0,
      dir: 'zoom-in',
    },
    {
      scale: 100,
      display: 100,
      step: 20,
      add: 0.5,
      dir: 'zoom-out',
    },
    {
      scale: 70,
      display: 200,
      step: 40,
      add: 0,
      dir: 'zoom-out',
    },
    {
      scale: 40,
      display: 500,
      step: 100,
      add: 0,
      dir: 'zoom-out',
    },
  ],
}
